.custom-notification {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: flex-start;
  justify-content: right;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  pointer-events: none;
  z-index: 9999;
}

.custom-notification.enter-done {
  opacity: 1;
  pointer-events: visible;
}

.custom-notification.exit {
  opacity: 0;
}

.custom-notification-content {
  width: 20%;
  height: 90px;
  border-radius: 20px;
  background: #ffffff;
  margin-top: 20px;
  margin-right: 10px;
  /* Shadow Box Iframe */
  box-shadow: 0px 0px 60px rgba(12, 30, 91, 0.35);
  border-radius: 20px;
  transition: all 0.3s ease-in-out;
  transform: translateX(200px);
  z-index: 1;
}

.custom-notification-content.custom-notification-small {
  max-width: 30%;
  min-height: 150px;
}

.custom-notification-close {
  background-color: rgb(253, 253, 253);
  border-radius: 100%;
  width: 35px;
  height: 35px;
  position: absolute;
  top: -3%;
  right: -2%;
  cursor: pointer;
  z-index: 2;
}
.custom-notification-close.custom-notification-close-button {
  color: #ff6966;
  cursor: pointer;
}

.custom-notification-close-button::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  content: "\274c"; /* use the hex value here... */
  font-size: 15px;
  line-height: 33px;
}
.custom-notification.enter-done .custom-notification-content {
  transform: translateX(0);
}

.custom-notification.exit .custom-notification-content {
  transform: translateX(200px);
}

.custom-notification-header {
  padding: 15px 30px;
}

.custom-notification-footer {
  padding: 10px 30px;
}

.custom-notification-title {
  margin: 0;
}

.custom-notification-body {
  padding: 15px 30px;
  border-top: 0px solid transparent;
  border-bottom: 0px solid transparent;
  max-height: 550px;
  overflow-y: auto;
}

.custom-notification-accept-button {
  width: 113px;
  height: 40px;
}
@media (max-width: 1440px) {
  .custom-notification-content {
    max-width: 60% !important;
    min-height: 387px;

    transition: all 0.3s ease-in-out;
    transform: translateX(-200px);
    z-index: 1;
  }

  .custom-notification-content.custom-notification-small {
    max-width: 35%  !important;
    min-height: 150px;
  }
}

